// app/api/auth/login/route.ts
import { NextRequest, NextResponse } from "next/server";
import bcrypt from "bcryptjs";
import { prisma } from "@/lib/db";
import { validateLoginRequest } from "@/lib/validators";
import { UnauthorizedError, ValidationError } from "@/lib/errors";
import { generateToken } from "@/lib/jwt-utils";
import { createSuccessResponse, createErrorResponse } from "@/lib/responses";

export async function POST(request: NextRequest) {
  try {
    const body = await request.json();

    // Validate input
    validateLoginRequest(body);

    const { mobile, password } = body;

    // Find user by mobile
    const user = await prisma.user.findUnique({
      where: { mobile },
    });

    if (!user) {
      throw new UnauthorizedError("Invalid mobile or password");
    }

    // Check if user is active
    if (user.status !== "ACTIVE") {
      throw new UnauthorizedError(
        `Account is ${user.status.toLowerCase()}. Please contact administrator.`,
      );
    }

    // Verify password
    const isPasswordValid = await bcrypt.compare(password, user.passwordHash);
    if (!isPasswordValid) {
      throw new UnauthorizedError("Invalid mobile or password");
    }

    // Generate token
    const token = generateToken({
      id: user.id,
      name: user.name,
      mobile: user.mobile,
      email: user.email || undefined,
      role: user.role,
      status: user.status,
    });

    // Create response with user data
    const responseData = {
      token,
      user: {
        id: user.id,
        name: user.name,
        mobile: user.mobile,
        email: user.email,
        role: user.role,
        status: user.status,
      },
    };

    return NextResponse.json(
      createSuccessResponse("Login successful", responseData),
      {
        status: 200,
        headers: {
          "Set-Cookie": `aerocity_auth=${JSON.stringify({ token, user: responseData.user })}; Path=/; HttpOnly; SameSite=Strict`,
        },
      },
    );
  } catch (error: any) {
    console.error("Login error:", error);

    if (error instanceof ValidationError) {
      return NextResponse.json(
        createErrorResponse(
          "Validation failed",
          error.message,
          error.code,
        ),
        { status: 400 },
      );
    }

    if (error instanceof UnauthorizedError) {
      return NextResponse.json(
        createErrorResponse(
          "Authentication failed",
          error.message,
          "UNAUTHORIZED",
        ),
        { status: 401 },
      );
    }

    return NextResponse.json(
      createErrorResponse(
        "Login failed",
        error.message || "Internal server error",
      ),
      { status: 500 },
    );
  }
}
